<template>
  <div class="timeline-view">
    <h2><span>TimeLine</span> <span class="chinese"></span></h2>
    <p>S.</p>
    <div class="preview clearfix">
      <xc-button :type="multiple ? 'info' : 'error'" @click="multiple = !multiple">multiple</xc-button>
      <xc-timeline :list="timelinedata" @click="clickhandler" :fontsize="12" :count="4" :multiple="multiple" size="s"></xc-timeline>
      <div class="code-wrapper clearfix">
        <input type="checkbox" id="code_timeline1" class="kakushi code-control-ck" checked>
        <div class="code-html full">
          <p class="code-head"><xc-icon type="vue"></xc-icon>  <label class="code-control" for="code_timeline1">&lt;<span>/</span>&gt;</label></p>
          <xc-code lang="html" :code="data2" line="3"></xc-code>
        </div>
      </div>
    </div>
    <h3><span>Event</span></h3>
    <xc-doc :list="event" type="event"></xc-doc>
    <h3><span>Attributes</span></h3>
    <xc-doc :list="doc"></xc-doc>
    <h3><span>Item Attributes</span></h3>
    <xc-doc :list="itemdoc"></xc-doc>
    <div class="data-view">
      <div class="code-wrapper clearfix">
        <input type="checkbox" id="code_transfer_data1" class="kakushi code-control-ck" v-model="ck1">
        <div class="code-json full">
          <p class="code-head json"><xc-icon type="json"></xc-icon> Data <label class="code-control" for="code_transfer_data1">&lt;<span>/</span>&gt;</label></p>
          <xc-code lang="json" :code="data1" line="30"></xc-code>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      timelinedata: [
        {id: 1, time: '20191224', title: 'Xx', icon: 'bar', content: 'XXXXXXXX', checked: true},
        {id: 2, time: '20191225', title: 'Xx', icon: 'plus', content: 'XXXXXXXX'},
        {id: 3, time: '20191226', title: 'Xx', icon: 'txt', content: 'XXXXXXXX', checked: true},
        {id: 4, time: '20191227', title: 'Xx', content: 'XXXXXXXX'},
        {id: 5, time: '20191228', title: 'Xx', content: 'XXXXXXXX'},
        {id: 6, time: '20191229', title: 'Xx', content: 'XXXXXXXX'},
        {id: 7, time: '20191230', title: 'Xx', content: 'XXXXXXXX'},
        {id: 8, time: '20191231', title: 'Xx', content: 'XXXXXXXX'},
        {id: 9, time: '20200101', title: 'Xx', content: 'XXXXXXXX'},
        {id: 10, time: '2020-01-02', title: 'Xx', content: 'XXXXXXXX'},
        {id: 11, time: '2020-01-03', title: 'Xx', content: 'XXXXXXXX'},
        {id: 12, time: '2020-01-04', title: 'Xx', content: 'XXXXXXXX'},
        {id: 13, time: '2020-01-05', title: 'Xx', content: 'XXXXXXXX'},
        {id: 14, time: '2020-01-06', title: 'Xx', content: 'XXXXXXXX'},
        {id: 15, time: '2020-01-07', title: 'Xx', content: 'XXXXXXXX'},
        {id: 16, time: '2020-01-08', title: 'Xx', content: 'XXXXXXXX'},
        {id: 17, time: '2020-01-09', title: 'Xx', content: 'XXXXXXXX'}
      ],
      list: ['pic', 'del', 'txt', 'ok', 'warn', 'error', 'bar', 'plus', 'bilibili', 'github', 'trash', 'copy', 'html', 'sass', 'json', 'vue', 'left', 'right'],
      doc: [
        {property: 'count', description: '', type: 'Number', default: '4'},
        {property: 'fontsize', description: 'px', type: 'Number', default: '12'},
        {property: 'size', description: 's & l', type: 'String', default: 'l'},
        {property: 'multiple', description: '', type: 'Boolean', default: 'false'},
        {property: 'list', description: '()', type: 'Array', default: '[Object]'}
      ],
      itemdoc: [
        {property: 'id', description: '', type: 'String/Number', default: ''},
        {property: 'time', description: '', type: 'String', default: ''},
        {property: 'content', description: '', type: 'String', default: ''},
        {property: 'icon', description: '()', type: 'String', default: ''},
        {property: 'checked', description: '()', type: 'Boolean', default: 'false'}
      ],
      event: [
        {eventName: 'click', description: ' / (multiple)', callbackParamet: 'Object / Array'}
      ],
      ck1: true,
      ck2: false,
      multiple: false,
      data1: `[
  {id: 1, time: '20191224', title: 'Xx', icon: 'bar', content: 'XXXXXXXX'},
  {id: 2, time: '20191225', title: 'Xx', icon: 'plus', content: 'XXXXXXXX'},
  {id: 3, time: '20191226', title: 'Xx', icon: 'txt', content: 'XXXXXXXX'},
  {id: 4, time: '20191227', title: 'Xx', content: 'XXXXXXXX'},
  {id: 5, time: '20191228', title: 'Xx', content: 'XXXXXXXX'},
  {id: 6, time: '20191229', title: 'Xx', content: 'XXXXXXXX'},
  {id: 7, time: '20191230', title: 'Xx', content: 'XXXXXXXX'},
  {id: 8, time: '20191231', title: 'Xx', content: 'XXXXXXXX'},
  {id: 9, time: '20200101', title: 'Xx', content: 'XXXXXXXX'},
  {id: 10, time: '2020-01-02', title: 'Xx', content: 'XXXXXXXX'},
  {id: 11, time: '2020-01-03', title: 'Xx', content: 'XXXXXXXX'},
  {id: 12, time: '2020-01-04', title: 'Xx', content: 'XXXXXXXX'},
  {id: 13, time: '2020-01-05', title: 'Xx', content: 'XXXXXXXX'},
  {id: 14, time: '2020-01-06', title: 'Xx', content: 'XXXXXXXX'},
  {id: 15, time: '2020-01-07', title: 'Xx', content: 'XXXXXXXX'},
  {id: 16, time: '2020-01-08', title: 'Xx', content: 'XXXXXXXX'},
  {id: 17, time: '2020-01-09', title: 'Xx', content: 'XXXXXXXX'}
]`,
      data2: `<xc-timeline :list="timelinedata" @click="clickhandler" :fontsize="12" :count="4" :multiple="multiple" size="s"></xc-timeline>`
    };
  },
  methods: {
    clickhandler(i) {
      console.log(i);
    }
  }
}
</script>

<style lang="scss">
  .timeline-view {
    padding: 20px;
  }
</style>


