<template>
  <div class="flextree">
    <h2><span>FlexTree</span> <span class="chinese"></span></h2>
    <p>.</p>
    <xc-button effect="spread" type="primary" @click="flextreetype = 'flex'">flex</xc-button>
    <xc-button effect="spread" type="primary" @click="flextreetype = 'list'">list</xc-button>
    <xc-button effect="spread" type="primary" @click="lineEdit = !lineEdit">addline-{{lineEdit}}</xc-button>
    <div class="preview clearfix" style="height: 400px">
      <xc-flextree :data="flextreeData" :type="flextreetype" :lineEdit="lineEdit"></xc-flextree>
    </div>
    <h3><span>Attributes</span></h3>
    <xc-doc :list="doc"></xc-doc>
  </div>
</template>

<script>
const treeData = {
  "name": "Root",
  "children":
  [
    {
      "name": "",
      title: "",
      "children": [
        {"name": "HTML & CSS"},
        {"name": "JavaScript"},
        {"name": "DOM"},
        {"name": "SVG"}
      ]
    }, {
      "name": "",
      "_children": [
        {"name": ""}
      ]
    },
    {
      "name": "",
      "children": [
        {"name": ""},
        {"name": "test"},
        {"name": ""},
        {"name": ""},
        {
          "name": "",
          "children":
          [
            {"name": ""},
            {"name": ""},
            {"name": ""}]
        },
        {"name": ""},
        {"name": ""},
        {"name": ""}
      ],
      "left": true
    },
    {
      "name": "",
      "left": true
    },
    {
      "name": "",
      "children": [
        {"name": "1"},
        {"name": "2"},
        {"name": "3"}
      ]
    }
  ]
};
export default {
  data() {
    return {
      doc: [
        {property: 'data', description: '', type: 'Object', default: ''},
        {property: 'type', description: 'flex & list', type: 'String', default: 'flex'},
        {property: 'lineEdit', description: '', type: 'Boolean', default: 'false'}
      ],
      flextreeData: treeData,
      flextreetype: 'flex',
      lineEdit: false
    };
  },
  methods: {
  }
}
</script>

<style lang="scss">
  .flextree {
    .preview {
      padding-bottom: .18rem;
    }
  }
</style>


